.catalog {
  width: 100%;
  padding: 50px 40px 70px;
  display: flex;
  justify-content: center;

  &__container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: calc((200px * 4) + (48px * 3));
  }

  &__product-card {
    margin: 0 48px 46px 0;
  }

  &__product-card:nth-child(4n) {
    margin-right: 0;
  }
}
