.product-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding: 32px 16px 16px;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background-color: #fff;

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto;
  }

  &__description {
    font-weight: 500;
    font-size: $font-size-12;
    line-height: 18px;
    text-align: left;
    color: $main-font-color;
    padding-top: 40px;
  }

  &__code {
    padding-top: 4px;
    padding-bottom: 16px;
    font-weight: 400;
    font-size: $font-size-10;
    line-height: 14px;
    text-align: left;
    color: $additional-font-color;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
  }

  &__review {
    font-weight: 400;
    font-size: $font-size-10;
    line-height: 14px;
    text-align: right;
    color: $main-font-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-top: 24px;
    padding-bottom: 16px;
  }

  &__price-title {
    font-weight: 400;
    font-size: $font-size-12;
    line-height: 18px;
    color: $additional-font-color;
  }

  &__price-value {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $main-font-color;
  }

  &__button {
    display: block;
    width: 166px;
    height: 40px;
    background-color: $blue-accent-color;
    border: 1px solid $blue-accent-color;
    color: #fff;
    border-radius: 5px;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    padding: 11px 0;
  }

  &__button:hover {
    background: #fff;
    color: $blue-accent-color;
  }
}
